@Component
export default class szTab extends Vue {
  // @Prop() private msg!: string;
  szD: SzDT[]=[];
  @Getter szz!: SzDT[];
  get tszD(){
    return this.szz;

  }
  @Mutation setSz!: (arg0: SzDT[]) => void;
  set tszD(data: SzDT[]){
    this.setSz(data);
  }
  water: number = 4;
  electric: number = 1;

  async created() {/*
    this.tszD=[{ id: new Date().getTime(), fjh: 0, zj: 0, sf: 0, df: 0 }]
    console.log(this.tszD)
    let tWater = LocalForage.getItem("water");
    let tElectric = LocalForage.getItem("electric");
    this.szD = (await LocalForage.getItem("sz")) as SzDT[];
    if (!this.szD) {
      this.szD = [];
    }
    this.water = (await tWater) as number;
    if (!this.water) {
      this.water = 4;
    }
    this.electric = (await tElectric) as number;
    if (!this.electric) {
      this.electric = 1;
    }*/
  }
  szAdd() {
    this.tszD.push({ id: new Date().getTime(), fjh: "0", zj: 0, sf: 0, df: 0 });
    this.tszD=this.tszD
  }
  szSub() {
    this.tszD.pop();
    this.tszD=this.tszD
  }
  saveSz() {
    this.tszD=this.tszD.map(e=>{
      if (e.sf===0)e.sf=this.water;
      if (e.df===0)e.df=this.electric;
      return e
    });
  }
}
