
















import { Component, Vue } from "vue-property-decorator";
import LocalForage from "localforage";
import { SzDT } from "@/store/szDT";

@Component
export default class dL extends Vue {
  szD: SzDT[] = [];
  saveSz() {
    let d = new Date();
    LocalForage.setItem(d.getFullYear() + "-" + (d.getMonth() + 1), this.szD);
  }
  async created() {
    this.szD = (await LocalForage.getItem("sz")) as SzDT[];
    if (!this.szD) {
      this.szD = [];
    } else {
      this.szD = this.szD.map(e => {
        return {
          ...e,
          sf: 0,
          df: 0
        };
      });
    }
  }
}
