






































































import { Component, Vue } from "vue-property-decorator";
import { SzDT } from "@/store/szDT";
import LocalForage from "localforage";
import {
  State,
  Getter,
  Action,
  Mutation,
  namespace
} from 'vuex-class'

@Component
export default class showD extends Vue {
  month: string = "";
  oMonth: string = "";
  fj: string = "";
  cSD: SzDT = { id: new Date().getTime(), fjh: "0", zj: 0, sf: 0, df: 0 };
  oSD: SzDT = { id: new Date().getTime(), fjh: "0", zj: 0, sf: 0, df: 0 };
  sSD: SzDT = { id: new Date().getTime(), fjh: "0", zj: 0, sf: 0, df: 0 };
  @State((state: { sz: { sz: SzDT[]; }; }) => state.sz.sz) szD!: SzDT[];
  @Action oneFjSz!: (arg0: string) => SzDT;
  @Action oneFjData!: (arg0: { key: string; fj: string; }) => SzDT;
  @Action oneData!: (arg0: string) => void;


  async fjC() {
    this.sSD = await this.oneFjSz(this.fj);
    this.cSD = await this.oneFjData({key:this.month,fj:this.fj});
    this.oSD = await this.oneFjData({key:this.oMonth,fj:this.fj});
  }
  async monthC() {
    let m = new Date(this.month);
    m.setMonth(m.getMonth() - 1);
    this.oMonth=m.getFullYear() + "-" + (m.getMonth() + 1);
    this.oneData(this.oMonth);
    this.oneData(this.month);
  }
  async created() {
/*    let tWater = LocalForage.getItem("water");
    let tElectric = LocalForage.getItem("electric");
    this.water = (await tWater) as number;
    if (!this.water) {
      this.water = 1;
    }
    this.electric = (await tElectric) as number;
    if (!this.electric) {
      this.electric = 1;
    }*/
    let d = new Date();
    this.month = d.getFullYear() + "-" + (d.getMonth() + 1);
    this.monthC();
  }
}
